				// Ê∑ªÂä†Êú¨Âú∞Â§áÁî®Êï∞ÊçÆÔºåÈò≤Ê≠¢‰∫ëÂáΩÊï∞Â§±Ë¥•
				localSchools: [
					'Âåó‰∫¨Â§ßÂ≠¶', 'Ê∏ÖÂçéÂ§ßÂ≠¶', 'Â§çÊó¶Â§ßÂ≠¶', '‰∏äÊµ∑‰∫§ÈÄöÂ§ßÂ≠¶', 'ÊµôÊ±üÂ§ßÂ≠¶',
					'Âçó‰∫¨Â§ßÂ≠¶', '‰∏≠ÂõΩ‰∫∫Ê∞ëÂ§ßÂ≠¶', 'Ê≠¶Ê±âÂ§ßÂ≠¶', '‰∏≠Â±±Â§ßÂ≠¶', 'Âçé‰∏≠ÁßëÊäÄÂ§ßÂ≠¶',
					'Âåó‰∫¨Â∏àËåÉÂ§ßÂ≠¶', 'Âé¶Èó®Â§ßÂ≠¶', 'ÂçóÂºÄÂ§ßÂ≠¶', 'ÂêâÊûóÂ§ßÂ≠¶', 'Ë•øÂÆâ‰∫§ÈÄöÂ§ßÂ≠¶',
					'ÂìàÂ∞îÊª®Â∑•‰∏öÂ§ßÂ≠¶', 'ÁîµÂ≠êÁßëÊäÄÂ§ßÂ≠¶', '‰∏úÂçóÂ§ßÂ≠¶', 'ÂõõÂ∑ùÂ§ßÂ≠¶', '‰∏≠ÂçóÂ§ßÂ≠¶'
				],
