					'ÁΩëÁªúÁ©∫Èó¥ÂÆâÂÖ®', 'ÁîµÂ≠ê‰ø°ÊÅØÂ∑•Á®ã', 'ÈÄö‰ø°Â∑•Á®ã', 'Ëá™Âä®Âåñ', 'Êú∫Ê¢∞Â∑•Á®ã',
					'ÂúüÊú®Â∑•Á®ã', 'Âª∫Á≠ëÂ≠¶', 'Â∑•ÂïÜÁÆ°ÁêÜ', '‰ºöËÆ°Â≠¶', 'ÈáëËûçÂ≠¶', 'ÁªèÊµéÂ≠¶',
					'Ê≥ïÂ≠¶', 'ÂåªÂ≠¶', 'ÁîüÁâ©ÁßëÂ≠¶', 'ÂåñÂ≠¶', 'Áâ©ÁêÜÂ≠¶', 'Êï∞Â≠¶', 'Ëã±ËØ≠',
					'Ê±âËØ≠Ë®ÄÊñáÂ≠¶', 'Êñ∞ÈóªÂ≠¶', 'ÂøÉÁêÜÂ≠¶', 'ÊïôËÇ≤Â≠¶'
