					}).catch(err => {
						console.error('Ëé∑ÂèñÂ≠¶Ê†°ÂàóË°®Â§±Ë¥•:', err);
						return { result: { code: -1, msg: err.message } };
					});
					
					uni.hideLoading();
					
					if (result.result && result.result.code === 0) {
						this.schools = result.result.data || [];
						console.log('Ëé∑ÂèñÂà∞Â≠¶Ê†°ÂàóË°®:', this.schools);
					} else {
