						return { result: { code: -1, msg: err.message } };
					});
					
					uni.hideLoading();
					
					if (result.result && result.result.code === 0) {
						this.majors = result.result.data || [];
						console.log('Ëé∑ÂèñÂà∞‰∏ì‰∏öÂàóË°®:', this.majors);
					} else {
						console.warn('‰ΩøÁî®Êú¨Âú∞‰∏ì‰∏öÂàóË°®');
						this.majors = this.localMajors;
					}
				} catch (e) {
					uni.hideLoading();
					console.error('Âä†ËΩΩ‰∏ì‰∏öÂàóË°®ÂºÇÂ∏∏:', e);
					// ‰ΩøÁî®Êú¨Âú∞Â§áÁî®Êï∞ÊçÆ
